/* PrismJS 1.29.0https://prismjs.com/download.html#themes=prism-dark&languages=markup+css+clike+javascript&plugins=line-highlight+line-numbers+show-invisibles+autolinker+show-language+highlight-keywords+remove-initial-line-feed+toolbar */

code[class*=language-],pre[class*=language-] {
	color:#fff;
	background:0 0;
	text-shadow:0 -.1em .2em #000;
	font-family:Consolas,Monaco,'Andale Mono','Ubuntu Mono',monospace;
	font-size:1em;
	text-align:left;
	white-space:pre;
	word-spacing:normal;
	word-break:normal;
	word-wrap:normal;
	line-height:1.5;
	-moz-tab-size:4;
	-o-tab-size:4;
	tab-size:4;
	-webkit-hyphens:none;
	-moz-hyphens:none;
	-ms-hyphens:none;
	hyphens:none;
	font-size:12px;
}
@media print {
	code[class*=language-],pre[class*=language-] {
		text-shadow:none
	}
}
:not(pre)>code[class*=language-],pre[class*=language-] {
	background:#282c34
}
pre[class*=language-] {
	/* padding:1em; */
	/* margin:.5em 0; */
	/* overflow:auto; */
	border-radius:.5em;
	box-shadow:1px 1px .5em #000 inset;
	width:642px;
}
:not(pre)>code[class*=language-] {
	padding:.15em .2em .05em;
	border-radius:.3em;
	box-shadow:1px 1px .3em -.1em #000 inset;
	white-space:normal
}
.token.cdata,.token.comment,.token.doctype,.token.prolog {
	color:#708090
}
.token.punctuation {
	opacity:.7
}
.token.namespace {
	opacity:.7
}
.token.boolean,.token.constant,.token.number,.token.property,.token.symbol,.token.tag {
	color:#905
}
.token.attr-name,.token.builtin,.token.char,.token.inserted,.token.selector,.token.string {
	color:#bce051
}
.language-css .token.string,.style .token.string,.token.entity,.token.operator,.token.url,.token.variable {
	color:#f4b73d
}
.token.atrule,.token.attr-value,.token.keyword {
	color:#07a
}
.token.important,.token.regex {
	color:#e90
}
.token.bold,.token.important {
	font-weight:700
}
.token.italic {
	font-style:italic
}
.token.entity {
	cursor:help
}
.token.deleted {
	color:red
}
pre[data-line] {
	position:relative;
	padding:1em 0 1em 3em
}
.line-highlight {
	position:absolute;
	left:0;
	right:0;
	padding:inherit 0;
	margin-top:1em;
	background:hsla(24,20%,50%,.08);
	background:linear-gradient(to right,hsla(24,20%,50%,.1) 70%,hsla(24,20%,50%,0));
	pointer-events:none;
	line-height:inherit;
	white-space:pre
}
@media print {
	.line-highlight {
		-webkit-print-color-adjust:exact;
		color-adjust:exact
	}
}
.line-highlight:before,.line-highlight[data-end]:after {
	content:attr(data-start);
	position:absolute;
	top:.4em;
	left:.6em;
	min-width:1em;
	padding:0 .5em;
	background-color:hsla(24,20%,50%,.4);
	color:#f4f1ef;
	font:bold 65%/1.5 sans-serif;
	text-align:center;
	vertical-align:.3em;
	border-radius:999px;
	text-shadow:none;
	box-shadow:0 1px #fff
}
.line-highlight[data-end]:after {
	content:attr(data-end);
	top:auto;
	bottom:.4em
}
.line-numbers .line-highlight:after,.line-numbers .line-highlight:before {
	content:none
}
pre[id].linkable-line-numbers span.line-numbers-rows {
	pointer-events:all
}
pre[id].linkable-line-numbers span.line-numbers-rows>span:before {
	cursor:pointer
}
pre[id].linkable-line-numbers span.line-numbers-rows>span:hover:before {
	background-color:rgba(128,128,128,.2)
}
pre[class*=language-].line-numbers {
	position:relative;
	padding-left:3.8em;
	counter-reset:linenumber
}
pre[class*=language-].line-numbers>code {
	position:relative;
	white-space:inherit
}
.line-numbers .line-numbers-rows {
	position:absolute;
	pointer-events:none;
	top:0;
	font-size:100%;
	left:-3.8em;
	width:3em;
	letter-spacing:-1px;
	border-right:1px solid #999;
	-webkit-user-select:none;
	-moz-user-select:none;
	-ms-user-select:none;
	user-select:none
}
.line-numbers-rows>span {
	display:block;
	counter-increment:linenumber
}
.line-numbers-rows>span:before {
	content:counter(linenumber);
	color:#999;
	display:block;
	padding-right:.8em;
	text-align:right
}
.token.cr,.token.lf,.token.space,.token.tab:not(:empty) {
	position:relative
}
.token.cr:before,.token.lf:before,.token.space:before,.token.tab:not(:empty):before {
	color:grey;
	opacity:.6;
	position:absolute
}
.token.tab:not(:empty):before {
	content:'\21E5'
}
.token.cr:before {
	content:'\240D'
}
.token.crlf:before {
	content:'\240D\240A'
}
.token.lf:before {
	content:'\240A'
}
.token.space:before {
	content:'\00B7'
}
.token a {
	color:inherit
}
div.code-toolbar {
	position:relative
}
div.code-toolbar>.toolbar {
	position:absolute;
	z-index:10;
	top:.3em;
	right:.2em;
	transition:opacity .3s ease-in-out;
	opacity:0
}
div.code-toolbar:hover>.toolbar {
	opacity:1
}
div.code-toolbar:focus-within>.toolbar {
	opacity:1
}
div.code-toolbar>.toolbar>.toolbar-item {
	display:inline-block
}
div.code-toolbar>.toolbar>.toolbar-item>a {
	cursor:pointer
}
div.code-toolbar>.toolbar>.toolbar-item>button {
	background:0 0;
	border:0;
	color:inherit;
	font:inherit;
	line-height:normal;
	overflow:visible;
	padding:0;
	-webkit-user-select:none;
	-moz-user-select:none;
	-ms-user-select:none
}
div.code-toolbar>.toolbar>.toolbar-item>a,div.code-toolbar>.toolbar>.toolbar-item>button,div.code-toolbar>.toolbar>.toolbar-item>span {
	color:#bbb;
	font-size:.8em;
	padding:0 .5em;
	background:#f5f2f0;
	background:rgba(224,224,224,.2);
	box-shadow:0 2px 0 0 rgba(0,0,0,.2);
	border-radius:.5em
}
div.code-toolbar>.toolbar>.toolbar-item>a:focus,div.code-toolbar>.toolbar>.toolbar-item>a:hover,div.code-toolbar>.toolbar>.toolbar-item>button:focus,div.code-toolbar>.toolbar>.toolbar-item>button:hover,div.code-toolbar>.toolbar>.toolbar-item>span:focus,div.code-toolbar>.toolbar>.toolbar-item>span:hover {
	color:inherit;
	text-decoration: none
}